<template lang="pug">
  #category-page
    div(v-if="category.title")
      vx-card(:title="category.title" :subtitle="category.description")
        ul.bordered-items(v-if="category.posts")
          li.py-2(v-for="post in category.posts" :key="post.link")
            router-link(:to="{ name: 'article-one', params: { article_id: post.link } }") {{ post.text }}

    router-link(:to="{ name: 'home' }") Назад
</template>

<script>
export default {
  name: 'category-one',
  data: () => ({
    category_id: '',
    category: {}
  }),
  watch: {
    '$route.params.category_id': function (category_id) {
      this.category_id = category_id
      // this.category = this.$store.getters.categoryById(this.category_id)
    }
  },
  async created() {
    this.category_id = this.$route.params.category_id
    this.category = this.$store.getters.categoryById(this.category_id)
  }
}
</script>
